export default class AttributionControl {
  private _container: any

  onAdd() {
    this._container = document.createElement('div')
    this._container.className = 'mapboxgl-ctrl'
    this._container.innerHTML =
      ' <a href="https://nextbillion.ai" target="_blank">NextBillion.ai</a>  <a href="https://openmaptiles.org" target="_blank">OpenMapTiles</a>  <a href="https://www.openstreetmap.org" target="_blank">OpenStreetMap</a> contributors'
    return this._container
  }

  onRemove() {
    this._container.parentNode.removeChild(this._container)
  }
}
