import * as MapboxGL from 'mapbox-gl'
import lightLayers from './styles/layers.json'
import darkLayers from './styles/layers-dark.json'
import baseStyle from './styles/style-min.json'
import symbolLayers from './styles/layers-symbol-light.json'

export function buildStyle(customSourceUrl?: string, customTheme?: string): MapboxGL.Style {
  let sourceUrl = 'https://static.nextbillion.io/nbmap/vt.json'
  let themeLayers = null

  if (typeof customSourceUrl === 'string' && customSourceUrl.length > 0) {
    sourceUrl = customSourceUrl
  }

  switch (customTheme) {
    case 'light':
      themeLayers = lightLayers
      break
    case 'dark':
      themeLayers = darkLayers
      break
    default:
      themeLayers = lightLayers
  }

  baseStyle['sources'] = {
    nextbillionai: {
      type: 'vector',
      url: sourceUrl,
      minzoom: 6,
      maxzoom: 18,
    },
  }

  baseStyle['layers'] = themeLayers as any

  return baseStyle
}

export function getSymbolLayers(): Array<MapboxGL.Layer> {
  return symbolLayers as Array<MapboxGL.Layer>
}
