import * as MapboxGL from 'mapbox-gl'
import 'mapbox-gl/dist/mapbox-gl.css'
import config from './../config'
import { NBMapOptions } from './options'
import { buildStyle, getSymbolLayers } from './style-ctl'
import { DirectionsResponse } from './../api/directions'
import { decodePolyline } from './decoder'
import Marker from './marker'
import Polyline from './polyline'
import AttributionControl from './attrib'
import palettes from './palettes'
import { SnapToRoadsResponse } from '../api/snap-to-roads'
import { Position } from './geo'

export default class Map {
  private readonly _map: MapboxGL.Map
  private readonly _options: NBMapOptions
  private _trafficLayer: MapboxGL.Layer | null
  private _symbolLayers: Array<MapboxGL.Layer>

  constructor(el: HTMLElement, options: NBMapOptions) {
    if (!config.API_KEY) {
      const msg = 'An APT Key is required to create a map!'
      alert(msg)
      throw new Error(
        msg +
          ' For example, add this line of code before creating a new map instance: nextbillion.setApiKey("your_api_key")',
      )
    }

    this._options = Object.assign({}, options)

    const defaultOptions: MapboxGL.MapboxOptions = {
      container: el || options.container,
      style: buildStyle(options.vectorTilesSourceUrl, options.theme),
      zoom: 12,
      center: [77.2223, 28.5934],
      attributionControl: false,
      transformRequest,
    }

    const mapboxOptions = Object.assign({}, defaultOptions, options)

    this._map = new MapboxGL.Map(mapboxOptions)

    const ac = new AttributionControl()

    this._map.on('load', () => {
      this._map.addControl(ac, 'bottom-right')
      this._addSymbolLayers()
    })

    this._trafficLayer = null
    this._symbolLayers = Array<MapboxGL.Layer>()
  }

  _addSymbolLayers() {
    this._symbolLayers = getSymbolLayers()
    this._symbolLayers.forEach((layer) => this._map.addLayer(layer))
  }

  _removeSymbolLayers() {
    this._symbolLayers.forEach((layer) => this._map.removeLayer(layer.id))
  }

  get map(): MapboxGL.Map {
    return this._map
  }

  setTheme(theme: string) {
    const newStyle = buildStyle(this._options.vectorTilesSourceUrl, theme)
    this._map.setStyle(newStyle)
    this._options.theme = theme
  }

  setSource(url: string) {
    const newStyle = buildStyle(url, this._options.theme)
    this._map.setStyle(newStyle)
    this._options.vectorTilesSourceUrl = url
  }

  toggleTraffic() {
    if (this._trafficLayer !== null) {
      // Turning off existing traffic vis
      this._map.removeLayer('live-traffic-layer')
      this._trafficLayer = null
      return
    }

    this._removeSymbolLayers()

    if (!this._map.getSource('live-traffic-speed')) {
      this._map.addSource('live-traffic-speed', {
        type: 'vector',
        url: 'https://vtspeed.inrix-dev.nextbillion.io/public.streets.json',
      })
    }

    const vectorLayerTraffic = {
      id: 'live-traffic-layer',
      source: 'live-traffic-speed',
      'source-layer': 'public.streets',
      type: 'line',
      paint: {
        'line-width': 4,
        'line-color': [
          'interpolate',
          ['linear'],
          ['number', ['get', 'lt_speed_bucket'], 3],
          0,
          'purple',
          1,
          'red',
          2,
          'orange',
          3,
          '#4db846',
        ],
      },
    }

    this._map.addLayer(vectorLayerTraffic as MapboxGL.Layer)
    this._trafficLayer = vectorLayerTraffic as MapboxGL.Layer
    this._addSymbolLayers()
  }

  renderDirections(data: DirectionsResponse): ((Marker | Polyline)[] | undefined)[] {
    if (!Array.isArray(data.routes)) {
      console.error('no valid routes to render')
      return []
    }
    // loop through all routes
    let i = 0
    const shapes = data.routes.map((route) => {
      // take a color for the polyline
      const color = palettes[i]
      i += 1
      if (i > palettes.length) {
        i = 0
      }

      const coords = decodePolyline(route.geometry, 5)
      if (coords.length < 2) {
        console.warn('Too few points, ignore this route')
        return
      }
      const start = coords[0]
      const end = coords[coords.length - 1]

      const polyline = new Polyline({
        path: [coords.map((item) => ({ lat: item[0], lng: item[1] }))],
        strokeColor: color,
        strokeWeight: 5,
        map: this,
      })

      const startMarker = new Marker({
        position: { lat: start[0], lng: start[1] },
        icon: 'red',
        map: this,
      })

      const endMarker = new Marker({
        position: { lat: end[0], lng: end[1] },
        icon: 'green',
        map: this,
      })

      this._map.fitBounds(
        [
          { lat: start[0], lng: start[1] },
          { lat: end[0], lng: end[1] },
        ],
        { padding: 100 },
      )

      return [startMarker, endMarker, polyline]
    })

    return shapes
  }

  // renderSnapToRoads is a helper function to draw snap-to-roads
  // results on the map.
  // If "original" is properly set, an extra polyline for reference
  // will be drawn on the map too.
  renderSnapToRoads(data: SnapToRoadsResponse, original?: Position[]): Polyline[] | null {
    if (!Array.isArray(data.snappedPoints) || data.snappedPoints.length === 0) {
      console.error('no valid points to render')
      return null
    }

    const result = []

    if (Array.isArray(original) && original.length > 0) {
      const referencePolyline = new Polyline({
        map: this,
        strokeColor: '#c3c6c6',
        strokeWeight: 5,
        path: [original],
      })

      result.push(referencePolyline)
    }

    const polyline = new Polyline({
      map: this,
      strokeColor: '#f1778d',
      strokeWeight: 5,
      path: [data.snappedPoints.map((p) => ({ lat: p.location.latitude, lng: p.location.longitude }))],
    })

    result.push(polyline)

    const start = data.snappedPoints[0].location
    const end = data.snappedPoints[data.snappedPoints.length - 1].location

    this._map.fitBounds(
      [
        { lat: start.latitude, lng: start.longitude },
        { lat: end.latitude, lng: end.longitude },
      ],
      { padding: 100 },
    )

    return result
  }

  removeDirections(directionShapes: ((Marker | Polyline)[] | undefined)[]) {
    directionShapes.forEach((shapeRow) => {
      if (!Array.isArray(shapeRow)) {
        return
      }
      shapeRow.forEach((shape) => shape.remove())
    })
  }

  removeSnapToRoads(snapToRoadsShapes: Polyline[]) {
    if (!Array.isArray(snapToRoadsShapes)) {
      return
    }

    snapToRoadsShapes.forEach((shape) => shape.remove())
  }

  flyTo(opts: MapboxGL.FlyToOptions) {
    this._map.flyTo(<MapboxGL.FlyToOptions>opts)
  }
}

function transformRequest(url: string, resourceType: MapboxGL.ResourceType): MapboxGL.RequestParameters {
  let key = config.API_KEY
  // TODO: change me!
  if (~url.indexOf('vtspeed.inrix-dev')) {
    key = 'a69473b33cce4d69b587e21151afa6b6'
  }
  if (resourceType === 'SpriteJSON' || resourceType === 'Tile' || resourceType === 'Source') {
    return {
      url: url + `?key=${key}`,
    }
  }
  return { url }
}
