import * as MapboxGL from 'mapbox-gl'
import OverlayOptions from './base-overlay'
import { Position } from './geo'

interface MarkerOptions extends OverlayOptions {
  position: Position
  color?: string
  icon?: string
}

export default class Marker {
  private readonly options: MarkerOptions
  private readonly _marker: MapboxGL.Marker

  constructor(options: MarkerOptions) {
    this.options = options
    if (isValidUrl(this.options.icon)) {
      const el = document.createElement('div')
      el.className = 'marker'
      el.style.backgroundImage = `url(${this.options.icon})`
      this._marker = new MapboxGL.Marker(el)
    } else {
      const color = this.options.icon || this.options.color || `#0077fa`
      this._marker = new MapboxGL.Marker({
        color
      })
    }
    this._marker.setLngLat([this.options.position.lng, this.options.position.lat]).addTo(this.options.map.map)
  }

  get marker(): MapboxGL.Marker {
    return this._marker
  }

  get map(): MapboxGL.Map {
    return this.options.map.map
  }

  remove() {
    this._marker.remove()
  }
}

const isValidUrl = (url: string | undefined) => {
  try {
    new URL(url + '')
  } catch (_) {
    return false
  }
  return true
}
